.SearchForm {
    display: flex;
    align-items: center;
    margin-top: 20px;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    outline: 1px solid lightgrey;
  }

.SearchFormButton {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-color: rgba(39, 170, 245, 0.1);
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  .SearchFormButton:hover {
    opacity: 1;
  }
  
  .SearchFormButtonLabel {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
  }

  .SearchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 15px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
  }

  .SearchFormInput::placeholder {
    font: inherit;
  }